import axios from "axios"

const getAxiosIntance = (isTokenRequired = false)=>{

  const headers = { }

  if(isTokenRequired){
    headers['Authorization'] = `Bearer ${localStorage.getItem("token")}`
  }

  const instance = axios.create({
    baseURL: 'https://jwt.vnadiradze.ge',
    headers
  });

  return instance;
}

export default getAxiosIntance;