import './login.css'
import { UserLogin } from '../../axios/httpServices/account';
import { useDispatch } from 'react-redux'
import { login } from '../../redux/slices/loginSlice';

const Login = () => {

  const dispatch = useDispatch()

  const formSubmitHandler = (e)=>{

    e.preventDefault();

    const form = new FormData(e.target);

    const formDateObj = {
      email: form.get("email"),
      password: form.get("password")
    }
    
    UserLogin(formDateObj)
      .then(res => {
        dispatch(login({
          isAdmin: res.data.user.is_admin === 1,
          token: res.data.token,
          email: res.data.user.email,
        }))

      })
      .catch(res => {
        alert("Invalid Data!")
      })

  }


  return (
    <div className="login-page">
      <form onSubmit={formSubmitHandler}>
        <div className="mb-3">
          <label className="form-label">Email address</label>
          <input type="email" required className="form-control" name="email" defaultValue="admin@gmail.com" />
        </div>
        <div className="mb-3">
          <label className="form-label">Password</label>
          <input type="password" required className="form-control" name="password" defaultValue="admin123" />
        </div>
        <button type="submit" className="btn btn-primary">Submit</button>
      </form>
    </div>
  );
}
 
export default Login;