import { useHistory } from "react-router";
import { Link } from "react-router-dom";
import { addPost } from "../../axios/httpServices/post";


const NewPost = () => {
  const history = useHistory()

  const formSubmitHandler = (e)=>{
    e.preventDefault();

    const form = new FormData(e.target);

    const newPost = {
      title: form.get("title"),
      body: form.get("body")
    }
    
    addPost(newPost)
      .then(() =>{
        alert("Added Successfully!")
        history.replace("/posts")
      })
      .catch(res => {
        console.log(res)
      })
  }


  return <div className="editPost">
    {
      <form onSubmit={formSubmitHandler}>
        <div className="mb-3">
          <label className="form-label">Title</label>
          <input type="text" required className="form-control" name="title" />
        </div>
        <div className="mb-3">
          <label className="form-label">Description</label>
          <textarea rows="5" required className="form-control" name="body"  />
        </div>
        <button type="submit" className="btn btn-primary">Add</button>
        <Link to="/posts" className="btn btn-success ms-2">Cancel</Link>
      </form>
    }
  </div>;
}
 
export default NewPost;