import getAxiosIntance from "../axiosInstance"


export const getAllPosts = ()=>{
  return getAxiosIntance(true).get("api/posts")
}

export const getPostById = (id)=>{
  return getAxiosIntance(true).get(`api/posts/${id}`)
}

export const updatePost = (id, updatedPost)=>{
  return getAxiosIntance(true).put(`api/posts/update/${id}`, updatedPost)
}

export const addPost = (newPost)=>{
  return getAxiosIntance(true).post(`api/posts/create`, newPost)
}

export const deletePost = (id)=>{
  return getAxiosIntance(true).delete(`api/posts/delete/${id}`)
}