import './App.css';
import Nav from './components/nav/nav';
import { Switch, Route, Redirect } from "react-router-dom";
import Home from './pages/home/home';
import Login from './pages/login/login';
import Posts from './pages/posts/posts';
import EditPost from './pages/editPost/editPost';
import { useSelector } from 'react-redux'
import NewPost from './pages/newPost/newPost';


const App = ()=>{

  const userDate = useSelector((state) => state.user)


  return (
    <div className="App">
      <Nav />

      <div className="container py-5">
        <Switch>
          <Route path="/" exact>
            <Home />
          </Route>
          <Route path="/login">
            {
              !userDate.isLoggedIn ? <Login /> : <Redirect to="/" />
            }
          </Route>
          <Route path="/posts">
            {
              userDate.isLoggedIn ? <Posts /> : <Redirect to="/" />
            }
          </Route>
          <Route path="/editpost/:id">
            {
              userDate.isLoggedIn && userDate.isAdmin ? <EditPost /> : <Redirect to="/" />
            }
          </Route>

          <Route path="/newpost">
            {
              userDate.isLoggedIn && userDate.isAdmin ? <NewPost /> : <Redirect to="/" />
            }
          </Route>
        </Switch> 
      </div>


    </div>
  );
}

export default App;
