import { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router";
import { Link } from "react-router-dom";
import { getPostById, updatePost } from "../../axios/httpServices/post";

const EditPost = () => {

  const { id } = useParams()
  const history = useHistory()
  const [post, setPost] = useState()

  useEffect(()=>{
    getPostById(id)
      .then(res => {
        setPost(res.data)
      })
  }, [id])

  const formSubmitHandler = (e)=>{
    e.preventDefault();

    const form = new FormData(e.target);




    const updatedPost = {
      id: +id,
      title: form.get("title"),
      body: form.get("body")
    }
    
    updatePost(+id, updatedPost)
      .then(() =>{
        alert("Updated Successfully!")
        history.replace("/posts")
      })
      .catch(res => {
        console.log(res)
      })
  }


  return <div className="editPost">
    {
      post !== undefined && <form onSubmit={formSubmitHandler}>
        <div className="mb-3">
          <label className="form-label">Title</label>
          <input type="text" required className="form-control" name="title" defaultValue={ post.title } />
        </div>
        <div className="mb-3">
          <label className="form-label">Description</label>
          <textarea rows="5" required className="form-control" name="body" defaultValue={ post.body } />
        </div>
        <button type="submit" className="btn btn-primary">Update</button>
        <Link to="/posts" className="btn btn-success ms-2">Cancel</Link>
      </form>
    }
  </div>;
}
 
export default EditPost;