import { NavLink } from "react-router-dom";
import { useSelector } from 'react-redux'
import { useDispatch } from "react-redux";
import { logout } from "../../redux/slices/loginSlice";


const Nav = () => {


  const userDate = useSelector((state) => state.user)
  const dispatch = useDispatch()

  return <nav className="navbar navbar-expand-lg navbar-light bg-light">
    <div className="container-fluid">
      <NavLink className="navbar-brand" to="/">Navbar</NavLink>
      <div className="collapse navbar-collapse" id="navbarNav">
        <ul className="navbar-nav">
          <li className="nav-item">
            <NavLink className="nav-link" to="/">Home</NavLink>
          </li>
          {
            userDate.isLoggedIn && <li className="nav-item ml-auto">
              <NavLink className="nav-link" to="/posts">Posts</NavLink>
            </li>
          }
        </ul>

        {
          !userDate.isLoggedIn && <ul className="navbar-nav ms-auto">
            <li className="nav-item">
              <NavLink className="nav-link" to="/login">Login</NavLink>
            </li>
          </ul>
        }

        {
          userDate.isLoggedIn && <ul className="navbar-nav ms-auto">
            <li className="nav-item">
              <button className="btn btn-success" onClick={()=> dispatch(logout())}>LogOut - { userDate.email }</button>
            </li>
          </ul>
        }
        
      </div>
    </div>
  </nav>
}
 
export default Nav;