import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { deletePost, getAllPosts } from "../../axios/httpServices/post";
import { useSelector } from 'react-redux'
import './posts.css'

const Posts = () => {

  const userData = useSelector((state) => state.user)
  const [posts, setPosts] = useState([])

  useEffect(()=>{
    getAllPosts()
      .then(res => {
        const posts = res.data;
        setPosts(posts)
      })
  }, [])

  const deleteHandler = (postId)=>{
    if(userData.isAdmin){
      deletePost(postId)
      .then(res => {
        // update Posts
        getAllPosts()
          .then(res => {
            const posts = res.data;
            setPosts(posts)
          })
      })
    }
  }

  return <div className="posts">
    <h3 className="text-center mb-5">All Posts</h3>

    { userData.isLoggedIn && userData.isAdmin ? <Link to="/newpost" className="btn btn-success">Add New Post</Link> : null }

    {
      posts.length > 0 && <ul className="posts">
        {
          posts.map(post => <li key={post.id}>
            <h4>{ post.title }</h4>
            <p>{ post.body }</p>
            { userData.isAdmin && <Link to={`/editpost/${post.id}`} className="btn btn-primary">Edit</Link> }
            { userData.isAdmin && <button className="btn btn-danger ms-2" onClick={()=>{deleteHandler(post.id)}}>Delete</button> }
          </li>)
        }
      </ul>
    }
  </div>;
}
 
export default Posts;